let demo = document.querySelector("#demo");
let string = "你好,我是酷酷酷" + "酷酷";
string = string.replace("<br>");
let n = 0;
demo.innerHTML = string.substring(0, n);
let step = () => {
  setTimeout(() => {
    n = n + 1;
    demo.innerHTML = string.substring(0, n);
    if (n < string.length) {
      step();
    }
  }, 100);
};
step();
